body {
  justify-content: center;
  align-items: center;
  min-height: 100svh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch:after {
  content: "";
  background: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  display: block;
  position: absolute;
}

.stopwatch__seconds-hand {
  background: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate;
  position: absolute;
}

.stopwatch__minutes-hand {
  background: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate;
  position: absolute;
}

.stopwatch--speed-up .stopwatch__minutes-hand {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__seconds-hand {
  animation-duration: 10s;
}

@keyframes rotate {
  from {
    transform: rotate(0)translateY(-50%);
  }

  to {
    transform: rotate(360deg)translateY(-50%);
  }
}

